<template>
    <div class="tech-box">
        <div class="tech-basemsg-line">
            <span class="job-name-title">{{ this.$t('job.job') }} {{ JobId.toString().padStart(3, "0") }} {{
                this.$t('job.edit') }}</span>
        </div>
        <div class="job-options-box_line_top1"></div>
        <div class="tech-basemsg-line">
            <span class="tech-num-3">{{ this.$t('job.jobName') }}</span>
            <InputText :module="module" class="tech-input-top" width="20vw" :disabled="true" :text="Name"
                @update:modelValue="Name = $event.text" :textValid="nameReason == ''" :validReason="nameReason">
            </InputText>
            <span class="tech-num-top">{{ this.$t('job.autoChoose') }}</span>
            <LeetxSwitch class="tech-input-2" :module="module" :value="AutoSelPset" :activeVal="true" :staticVal="false"
                :height="2" @sentVal="AutoSelPset = $event"></LeetxSwitch>
        </div>
        <div class="job-options-box_line_top2"></div>
        <div class="tech-basemsg-line">
            <span class="tech-num-3">{{ this.$t('job.ngCount') }}</span>
            <InputVal :imprprChrctr="limitBtn" :minNum="0" :maxNum="9999" :module="module" :sensorValue="MaxNGCount"
                width="10rem" class="tech-input-3" @update:sensorValue="MaxNGCount = $event">
            </InputVal>
            <span class="tech-num-top">{{ this.$t('job.reused') }}</span>
            <LeetxSwitch :module="module" :value="Reused" :activeVal="true" :staticVal="false" :height="2"
                class="tech-input-4" @sentVal="Reused = $event"></LeetxSwitch>
        </div>
        <div class="job-options-box_line_top3"></div>
        <div class="operate-line">
            <div class="operate-box-stroke" @click="addParallelPf">
                <svg-loader class="pfIcon" icon="Addparallel" size="2.5rem"></svg-loader>
                <br>
                <span>{{ this.$t('job.parallelAdd') }}</span>
            </div>
            <div>
                <span class="line"></span>
            </div>
            <div class="operate-box-stroke" @click="addSerialPf">
                <svg-loader class="pfIcon" icon="Addserial" size="2.5rem"></svg-loader>
                <br>
                <span>{{ this.$t('job.serialAdd') }}</span>
            </div>
            <div>
                <span class="line"></span>
            </div>
            <div class="operate-box" @click="deletePf">
                <svg-loader class="pfIcon" icon="TrashOne" size="2.5rem"></svg-loader>
                <br>
                <span class="delete-span">{{ this.$t('job.delete') }}</span>
            </div>
        </div>
        <div class="job-options-box_line_top4"></div>
        <div class="job-options-box" v-if="JobItems.length != 0">
            <div v-for="(items, i) in JobItems" :key="i" class="job-options-box_1">
                <div v-if="i" class="job-options-box_line1">
                </div>
                <div class="job-options-box_f">&</div>
                <div v-for="(pset, j) in items" :key="j" class="job-options-box_2">
                    <div class="job-options-box_line2">
                    </div>
                    <div :class="{ 'job-options-box_4': true, 'job-options-box_4_active': active_item_i == i && active_item_j == j }"
                        @click="selectFile(i, j)">
                        <svg-loader :class="{ 'file-static-icon': true, }"
                            :icon="active_item_i == i && active_item_j == j ? 'ActiveJob' : 'StaticJob'"></svg-loader>
                        <span class="pf-static-num">{{ pset.Pset }}</span>
                        <span class="file-name">{{ this.$t('job.num') }}：{{ pset.BatchSize }}</span>
                    </div>
                </div>
            </div>
        </div>

        <!-- 展示当前选中的工步 -->
        <div class="pro-step-data" v-if="curJobId != -1">
            <div class="tech-basemsg-line">
                <span class="tech-pset">{{ this.$t('job.pset') }}</span>
                <NewSelect :isScroll="true" :bottom="true" :width="36.5" :module="module" :realVal="currentPset.Pset"
                    :isAllowChng="true" class="tech-input-1" fontSize="1.25rem" :options="profileOptionNameLst"
                    @valChng="setParams('Pset', ($event))">
                </NewSelect>
            </div>

            <div class="tech-basemsg-line">
                <span class="tech-num">{{ this.$t('job.num') }}</span>
                <InputVal :imprprChrctr="limitBtn" :maxNum="9999" :minNum="1" :module="module"
                    :sensorValue="currentPset.BatchSize" fontSize="1.25rem" width="13.5rem" class="tech-input-1"
                    @updateData="setParams('BatchSize', ($event.sensorValue))"></InputVal>

            </div>
            <div class="tech-basemsg-line">
                <span class="tech-num">{{ this.$t('job.okCountMode') }}</span>
                <NewSelect :bottom="true" fntSize="1.25rem" valLeft="-0.3rem" :module="module" :width="13.5"
                    :realVal="currentPset.OnlyCntOK" :isAllowChng="true" class="tech-input-1" :options="okCountOpt"
                    @valChng="setParams('OnlyCntOK', ($event))">
                </NewSelect>
                <span class="tech-num-2">{{ this.$t('job.reverseAng') }}</span>
                <InputVal :module="module" class="tech-input" :maxNum="500" :minNum="0" height="3.125rem" fontSize="1.25rem"
                    width="12rem" uname="angle" :showUnitName="false" v-model:sensorValue="currentPset.ReverseOk">
                </InputVal>
            </div>

            <div class="tech-basemsg-line">
                <span class="tech-num">{{ this.$t('job.inReduceCountMode') }}</span>
                <NewSelect :bottom="true" :module="module" :width="13.5" :realVal="currentPset.DecLoosening"
                    :isAllowChng="true" fntSize="1.25rem" valLeft="-0.3rem" class="tech-input-1" :options="inReduceCount"
                    @valChng="setParams('DecLoosening', ($event))">
                </NewSelect>

                <span class="tech-num-2">{{ this.$t('job.reverseMode') }}</span>
                <NewSelect :bottom="true" :module="module" :width="12" :realVal="currentPset.LooseningMode"
                    :isAllowChng="true" fntSize="1.25rem" valLeft="-0.3rem" class="tech-input" :options="reverseMode"
                    @valChng="setParams('LooseningMode', ($event))">
                </NewSelect>
            </div>

        </div>
        <LeetxButton :module="module" width="30.3125rem" height="3.75rem" @click="confirm" textColor="#fff"
            :text="$t('knowhat.save')" fontFamily="'SourceHanSansCN-Bold'" fontSize="2.25rem" bgcColor="#00c155"
            class="confirm-box"></LeetxButton>
    </div>
</template>

<script>
import { deepCopy } from "@/utils/deepCopy";
import {
    genJobProfile,
} from "@/components/profiles/profileHelper";
import InputText from "@/components/input/InputText.vue";
import InputVal from "@/components/input/InputVal.vue";
import NewSelect from "@/components/select/NewSelect.vue";
import { messageApi } from "@/components/message/message";
// 激活文件
import { pfModJobApi, curPfJobApi, curPfJobListApi } from "@/api/profile/profile_job";
import { curPfListApi } from "@/api/profile/profile";
// import getEmptyProfile from '../utils/empty_profile'
import cnfrmWin from '@/dialogs/common/confirm/cnfrm.js'
import langvue from '@/plugins/langVue'
export default {
    components: {
        NewSelect,
        InputText,
        InputVal,
    },
    data() {
        return {
            module: "JOB",
            profile: {},
            // 通过job获取文件成功以后才赋值,
            curJobId: -1,
            need_check_edit: false,
            delPsetNum: 0,

            profileNameLst: [],

            defaultPset: {
                "Pset": 0,
                "RetriesLimit": true, //重复次数限制
                "Retries": 1, //重复次数
                "BatchSizeLimit": true, //pset打的次数次数限制
                "BatchSize": 10, //该pset打的次数
                "LooseningMode": 0, //0 永远不反松,1 始终反松 2,不合格时反松
                "OnlyCntOK": 1, //合格时才计数
                "ForceBatchStatus": 0, //0 批次不合格,1批次合格
                "DecLoosening": 0, //0 永远不减计数,1 反松时减计数 2,反松OK后减计数
                "ReverseOk": 0,
            },
            /**
             * 工艺文件列表
             */
            // profileOptionNameLst: { [`0 | ${this.$t('job.psetZero')}`]: 0 },
            profileOptionNameLst: {},
            active_item_i: 0,
            active_item_j: 0,
            Name: "job1", //Job名称
            JobId: 1, //JobId
            Descr: "", // 描述
            Ver: "1.0.0",
            Mode: 1,
            Reused: true, //输出循环
            JobSelSource: 0, //与Pset选择源一致,
            AutoSelPset: false, //自动选择Pset
            MaxNGCount: 10,
            global_json_name: '',
            global_json_maxNg: '',
            global_json_AutoSelPsetg: '',
            global_json_reused: '',
            global_job_params_data: '',
            JobItems: [],
            verifyZero: true,
            okCountOpt: {},
            inReduceCount: {},
            reverseMode: {},
            limitBtn: ['.', '-']
        };
    },
    beforeRouteLeave(to, from, next) {
        this.saveVerify()
        if (!this.verifyZero) {
            messageApi.warning([this.$t('job.psetNumNoZero')]);
            next(false);
            return;
        }
        if (this.nameReason != "" && this.curPsetId != -1) {
            messageApi.warning([this.$t('job.jobInvalidName')]);
            next(false);
            return;
        }
        let global_job_unchanged = this.global_job_params_data == JSON.stringify(this.profile)
        let global_name = this.global_json_name == JSON.stringify(this.Name)
        let global_max = this.global_json_maxNg == JSON.stringify(this.MaxNGCount)
        let global_auto = this.global_json_AutoSelPsetg == JSON.stringify(this.AutoSelPset)
        let global_reused = this.global_json_reused == JSON.stringify(this.Reused)
        if (global_job_unchanged && global_name && global_max && global_auto && global_reused) {
            next()
        } else {
            let title = this.$t('setcontrol.saveJob')
            let confirmText = this.$t('setcontrol.yes')
            let cancelText = this.$t('setcontrol.no')
            let msg = this.$t('setcontrol.isSaveJob')
            cnfrmWin({
                title,
                confirmText,
                cancelText,
                msg
            }).then(async () => {
                await this.confirm()
                next();
            })
                .catch(() => {
                    next();
                });
        }
    },
    methods: {
        initOptions() {
            let allCount = this.$t('job.allCount')
            let okCount = this.$t('job.okCount')
            let inReduceCount = this.$t('job.inReduceCount')
            let neverReduceCount = this.$t('job.neverReduceCount')
            let inOkReduceCount = this.$t('job.inOkReduceCount')
            let neverReverse = this.$t('job.neverReverse')
            let allowReverse = this.$t('job.allowReverse')
            let nokReverse = this.$t('job.nokReverse')
            this.okCountOpt = { [allCount]: 0x00, [okCount]: 0x01 }
            this.inReduceCount = { [neverReduceCount]: 0x00, [inReduceCount]: 0x01, [inOkReduceCount]: 0x02 }
            this.reverseMode = { [neverReverse]: 0x00, [allowReverse]: 0x01, [nokReverse]: 0x02 }
        },
        selectFile(i, j) {
            this.active_item_i = i;
            this.active_item_j = j;
        },
        setParams(key, val) {
            this.JobItems[this.active_item_i][this.active_item_j][key] = val
        },

        setboolParams(key, val) {
            this.JobItems[this.active_item_i][this.active_item_j][key] = val
        },
        addParallelPf() {
            this.JobItems[this.active_item_i].splice(this.active_item_j + 1, 0, deepCopy(this.defaultPset))
            this.active_item_j++

        },
        addSerialPf() {
            this.JobItems[this.active_item_i + 1] = this.JobItems[this.active_item_i + 1] ? this.JobItems[this.active_item_i + 1] : []
            this.JobItems[this.active_item_i + 1].unshift(deepCopy(this.defaultPset))
            this.active_item_i++
            this.active_item_j = 0
        },
        deletePf() {
            console.log("delete")
            let title = this.$t('job.delPset')
            let confirmText = this.$t('tchnlgy.yes')
            let cancelText = this.$t('tchnlgy.no')
            let msg = this.$t("job.isDelPset");
            cnfrmWin({ title, confirmText, cancelText, msg }).then(() => {
                const prevRowIndex = Math.max(this.active_item_i - 1, 0);
                this.JobItems[this.active_item_i].splice(this.active_item_j, 1);

                if (this.JobItems[this.active_item_i].length == 0) {
                    this.JobItems.splice(this.active_item_i, 1);
                    this.active_item_i = prevRowIndex;
                    this.active_item_j = 0;
                } else {
                    this.active_item_j = Math.min(this.active_item_j, this.JobItems[this.active_item_i].length - 1);
                }
                // 默认需要有一个默认pset
                if (this.JobItems.length == 0) {
                    this.JobItems = [[deepCopy(this.defaultPset)]];
                    this.profile.JobItems = this.JobItems;
                }
            });

        },
        saveVerify() {
            for (let i = 0; i < this.profile.JobItems.length; i++) {
                for (let j = 0; j < this.profile.JobItems[i].length; j++) {
                    const pset = this.profile.JobItems[i][j].Pset;
                    if (pset === 0) {
                        this.verifyZero = false
                        messageApi.error([this.$t('job.psetNumNoZero')]);
                        return;
                    } else {
                        this.verifyZero = true
                    }
                }
            }
        },
        async confirm() {
            if (this.nameReason != "" && this.curPsetId != -1) {
                messageApi.warning([this.$t('job.jobInvalidName')]);
                return;
            }
            this.profile.Name = this.Name;
            this.profile.JobId = this.JobId;
            this.profile.Descr = this.Descr;
            this.profile.Ver = this.Ver;
            this.profile.Mode = this.Mode;
            this.profile.Reused = this.Reused;
            this.profile.JobSelSource = this.JobSelSource;
            this.profile.AutoSelPset = this.AutoSelPset;
            this.profile.MaxNGCount = this.MaxNGCount;
            let flag = true;
            for (let i = 0; i < this.profile.JobItems.length; i++) {
                let tempArr = []; // 用于存储当前行的 Pset 值
                for (let j = 0; j < this.profile.JobItems[i].length; j++) {
                    const pset = this.profile.JobItems[i][j].Pset;

                    if (pset === 0) {
                        flag = false;
                        this.verifyZero = false
                        messageApi.error([this.$t('job.psetNumNoZero')]);
                        return;
                    } else {
                        this.verifyZero = true
                    }

                    // Pset 必须为pset列表中已有的值
                    // this.profileOptionNameLst
                    if (!Object.values(this.profileOptionNameLst).includes(pset)) {
                        flag = false;
                        messageApi.error([this.$t('job.psetNotExist', [pset])]);
                        return;
                    }
                    if (tempArr.includes(pset)) {
                        flag = false;
                        messageApi.error([this.$t('job.noRepeat')]);
                        return;
                    } else {
                        tempArr.push(pset);
                    }
                }
            }
            this.need_check_edit = false;
            if (flag) {
                await this.sendFileToServerApi(this.profile, this.saveSuccess);
            }
        },
        //让放大的工步返回普通状态
        backStaticBox() {
            this.activeBox = -1;
        },
        // 向后台发送数据,成功执行success,失败执行failure;
        async sendFileToServerApi(Profile, success) {
            if (!this.enabledEdit) {
                return messageApi.warning([this.$t('tchnlgy.isRunning'), this.$t('tchnlgy.noModified')]);
            }
            try {
                const result = await pfModJobApi({ Profile });
                this.need_check_edit = false;
                success && success(result);
            } catch (err) {
                console.log("pfModJobApi profile error ", err);
                messageApi.error([langvue.global.t('job.saveFail')]);
            }
        },
        // 通过job获取工艺文件内容,成功执行success,失败执行failure,
        getFileContentByJobApi(job, success, failure) {
            curPfJobApi(job)
                .then((result) => {
                    success && success(result);
                })
                .catch((err) => {
                    console.log("getFileContent profile error ", err);
                    failure && failure(err);
                });
        },
        // 获取当前工艺文件
        getCurJobProfile(job) {
            if (job && job > 0) {
                this.getFileContentByJobApi(
                    job,
                    (result) => {
                        if (result.status === 0) {
                            let file = result.data.Profile;
                            this.profile = file
                            this.setFileContent(file);
                            this.curJobId = job;
                            this.need_check_edit = true;
                            this.global_job_params_data = JSON.stringify(this.profile)
                        } else {
                            this.curJobId = -1;
                            this.need_check_edit = false;
                            messageApi.warning([this.$t('job.getFileFail')]);
                        }
                    },
                    () => {
                        this.curJobId = -1;
                        this.need_check_edit = false;
                        messageApi.warning([this.$t('job.getFileFail')]);
                    }
                );
            }
        },
        // 设置文件内容
        setFileContent(file) {
            // this.profile_name = file.Name;
            this.profile = file;
            this.Name = this.profile.Name;
            // this.JobId = this.profile.JobId ;
            this.Descr = this.profile.Descr;
            this.Ver = this.profile.Ver;
            this.Mode = this.profile.Mode;
            this.Reused = this.profile.Reused;
            this.JobSelSource = this.profile.JobSelSource;
            this.AutoSelPset = this.profile.AutoSelPset;
            this.MaxNGCount = this.profile.MaxNGCount;
            this.JobItems = this.profile.JobItems;
            this.global_json_name = JSON.stringify(this.Name);
            this.global_json_maxNg = JSON.stringify(this.MaxNGCount);
            this.global_json_AutoSelPsetg = JSON.stringify(this.AutoSelPset);
            this.global_json_reused = JSON.stringify(this.Reused);
        },
        // 初始化一个空的工艺文件
        initEmptyProfile() {
            this.profile = genJobProfile(2);
            this.setFileContent(this.profile);
        },
        excuteDefaultAction() {
            // this.resetData();
            this.JobId = parseInt(this.$route.query.job);
            this.curJobId = this.JobId;
            if (this.curJobId > 0) {
                this.getCurJobProfile(this.curJobId);
            }
        },
        // 获取文件列表
        getPfList(success = undefined) {
            curPfListApi()
                .then((result) => {
                    if (result.status == 9127) {
                        return;
                    }
                    if (result.status != 0) {
                        return;
                    }
                    // this.profileOptionNameLst = {};
                    result.data.sort((a, b) => {
                        return a.pset - b.pset
                    }).forEach((file) => {
                        this.profileOptionNameLst[file.pset + ' | ' + file.name] = file.pset
                    });
                    success && success();
                })
                .catch(() => { });
        },

        // 获取文件列表
        getJobPfList(success = undefined) {
            curPfJobListApi()
                .then((result) => {
                    if (result.status == 9127) {
                        return;
                    }
                    if (result.status != 0) {
                        return;
                    }
                    // this.profileNameLst = [];
                    // this.profileOptionNameLst = {};
                    result.data.forEach((file) => {
                        if (file.job != this.curJobId)
                            this.profileNameLst.push(file.name);
                    });
                    success && success();
                })
                .catch(() => { });
        },

        saveSuccess(result) {
            if (result.status == 0) {
                this.global_job_params_data = JSON.stringify(this.profile)
                this.global_json_name = JSON.stringify(this.Name)
                this.global_json_maxNg = JSON.stringify(this.MaxNGCount)
                this.global_json_AutoSelPsetg = JSON.stringify(this.AutoSelPset)
                this.global_json_reused = JSON.stringify(this.Reused)
                // this.profile_json = JSON.stringify(this.seqItems);
                // let msg = this.$t('settable.success')
                messageApi.success([this.$t('setop.saveSuc')])
            } else if (result.status == 9131) {
                messageApi.warning([this.$t('tchnlgy.fileUnchanged')]);
            } else if (result.status == 9125) {
                messageApi.warning([this.$t('tchnlgy.fileNotExit')]);
            } else if (result.status == 9128) {
                messageApi.warning([this.$t('tchnlgy.versionInconsistent')]);
            } else if (result.status == 9129) {
                messageApi.warning([this.$t('tchnlgy.modifyFileFail')]);
            } else {
                messageApi.error([this.$t('tchnlgy.saveFail'), [`[${result.status}]`]]);
            }
        },
        //是否删除正在编辑的工艺
        checkDelPfPset() {
            this.$bus.on('delPset', (data) => {
                this.delPsetNum = data.pset
                if (this.profile == '') return
            })
        }
    },
    computed: {
        currentPset() {
            return this.JobItems[this.active_item_i][this.active_item_j];
        },
        currentPsetlist() {
            return this.JobItems[this.active_item_i][this.active_item_j];
        },
        enabledEdit() {
            return (
                (this.curJobId == this.$store.state.g_status.pset &&
                    this.$store.state.g_status.busy) == false
            );
        },
        nameReason() {
            // 没有正在编辑的文件,文件名可以为空
            if (this.curJobId == -1) return -1;
            // 1代表文件名为空,2代表工艺名称重复,-1代表合法的工艺文件名
            if (this.Name.length == 0) {
                return this.$t('dlgprogram.jobNameNotEmpty')
            }
            if (
                this.Name && this.profileNameLst.length > 0 &&
                this.profileNameLst.indexOf(this.Name) != -1
            ) {
                return this.$t('dlgprogram.jobNameRepeat')
            }
            return "";
        },
        enabled() {
            if (
                "JOB" in this.$store.state.f_status.permissions &&
                this.$store.state.f_status.permissions["JOB"] == 4
            )
                return true;
            else return false;
        },
        pointerEvent() {
            return this.enabled ? "auto" : "none";
        },
        opacity() {
            return this.enabled ? 1 : 0.5;
        },
    },
    created() {
        this.getPfList();
        this.getJobPfList();
        this.initEmptyProfile();
        this.checkDelPfPset()
        this.initOptions()
    },
    watch: {
        "$route.query.job": {
            immediate: true,
            handler: function () {
                this.excuteDefaultAction();
            },
        }
    },
};
</script>

<style scoped>
.tech-box {
    position: relative;
    height: 100%;
    overflow: hidden;
    color: var(--C3B);
}

.operate-line {
    position: relative;
    top: 2rem;
    left: 1.3rem;
    width: 95vw;
    display: flex;
    box-sizing: border-box;
    border-bottom: 1px solid var(--CD4);
    pointer-events: v-bind(pointerEvent);
    opacity: v-bind(opacity);
    background: #EEEEEE;
    border-radius: 9px;
}

.operate-line div {
    flex: 1;
    padding: 5px;
    position: relative;
}

.line {
    display: block;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 2.05rem;
    width: 1px;
    background-color: var(--CD4);
}

.operate-line .operate-box {
    flex: 3;
    margin-top: 0.3rem;
    font-size: var(--font1_625);
    /* font-weight: 500; */
    color: #3B3B3B;
